import React from "react";
import ReactDOM from "react-dom";

import Avatar from './Avatar.js';
import Label from './Label.js';

import { repoName, date } from './helpers.js';
import USERS from './users.js';

class App extends React.Component {
  constructor(props) {
    super(props);
    const githubToken = localStorage.getItem('github_token')
    const favorites = JSON.parse(localStorage.getItem('favorites')) || []

    this.state = {
      sidebarVisible: true,
      githubToken,
      page: 1,
      favorites,
      favoriteName: '',

      org: "edlio",
      repo: "",
      closed: "",
      merged: "",
      created: "",
      assignee: "",
      author: "",
      status: "open",
      is: "issue",
      sort: "",
      order: "desc"
    };
  }

  componentDidMount() {
    this.search()
  }

  get query() {
    const {
      status,
      repo,
      closed,
      merged,
      is,
      org,
      assignee,
      created,
      author
    } = this.state;
    let query = "";

    if (status) {
      query += `+is:${status}`;
    }

    if (closed) {
      query += `+closed:${closed}`;
    }

    if (merged) {
      query += `+merged:${merged}`;
    }

    if (created) {
      query += `+created:${created}`;
    }

    if (assignee) {
      query += `+assignee:${assignee}`;
    }

    if (author) {
      query += `+author:${author}`;
    }

    if (repo) {
      query += `+repo:${org}/${repo}`;
    } else {
      query += `+org:${org}`;
    }

    if (is) {
      query += `+is:${is}`;
    }

    return query;
  }

  get pageCount() {
    const { results } = this.state;

    const allPages = Math.ceil(results.total_count / this.issueCountPerPage);

    return Math.min(allPages, 1000 / this.issueCountPerPage);
  }

  get issueCountPerPage() {
    return 20
  }

  get sortFields() {
    return ["created", "updated", "comments", "reactions", "interactions"];
  }

  get currentSearch() {
    const {org, repo, closed, merged, created, assignee, author, status, is, sort, order} = this.state

    return { org, repo, closed, merged, created, assignee, author, status, is, sort, order }
  }

  handleTokenChange(e) {
    const githubToken = e.target.value

    localStorage.setItem('github_token', githubToken);
    this.setState({ githubToken })
  }

  handleClearTokenClick() {
    localStorage.removeItem('github_token')

    this.setState({ githubToken: null })
  }

  async handleFavoriteClick(search) {
    await this.setState(search)
    await this.search()
  }

  handleAddFavoriteClick() {
    const { favorites, favoriteName } = this.state;

    if (!favoriteName) {
      return
    }

    const favorite = {
      name: favoriteName,
      search: this.currentSearch,
      id: Math.random().toString(36).substr(2, 9)
    }

    favorites.push(favorite)
    localStorage.setItem('favorites', JSON.stringify(favorites));
    this.setState({ favorites, favoriteName: '' })
  }

  async handleSearch() {
    await this.setState({ page: 1 });

    this.search();
  }

  async handlePageClick(page) {
    await this.setState({ page });

    this.search();
  }

  async handleAuthorClick(user) {
    await this.setState({ author: user.login, page: 1 });

    this.search();
  }

  async handleAssigneeClick(user) {
    await this.setState({ assignee: user.login, page: 1 });

    this.search();
  }

  async handleRepoClick(repo) {
    await this.setState({ repo, page: 1 });

    this.search();
  }

  async search() {
    const { page, sort, order, githubToken } = this.state;
    const url = `https://api.github.com/search/issues?q=${
      this.query
    }&page=${page}&sort=${sort}&order=${order}&per_page=${this.issueCountPerPage}`;

    const headers = {};
    if (githubToken) {
      headers.Authorization = `token ${githubToken}`
    }

    const res = await fetch(url, { headers });
    const results = await res.json();

    this.setState({ results });
  }

  render() {
    const {
      favorites,
      favoriteName,
      githubToken,
      sort,
      order,
      status,
      author,
      results,
      repo,
      closed,
      merged,
      is,
      page,
      org,
      assignee,
      created,
      sidebarVisible
    } = this.state;

    return (
      <div
        style={{
          fontSize: 13,
          fontFamily:
            "-apple-system,BlinkMacSystemFont,Segoe UI,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol",
          display: "flex"
        }}
      >
        <div style={{ flexGrow: 1 }}>
          {results && results.message}

          {results && results.items && (
            <div>
              <table style={{ width: '100%' }}>
                <tr>
                  <th>Results {results.total_count}</th>
                  <th>Author</th>
                  <th>Assignees</th>
                  <th>Created</th>
                  <th>Updated</th>
                  <th>Closed</th>
                </tr>
                {results.items.map(item => (
                  <tr>
                    <td>
                      <b>{item.pull_request && "PR "}</b>{" "}
                      <a
                        href="#"
                        onClick={() => this.handleRepoClick(repoName(item))}
                      >
                        {repoName(item)}
                      </a>
                      #{item.number}{" "}
                      <a
                        style={{ color: item.state === "closed" && "gray" }}
                        target="_blank"
                        href={item.html_url}
                      >
                        {item.title}
                      </a>{" "}
                      {item.comments > 0 && item.comments + ' 💬'}
                      {item.labels.map(label => (
                        <Label label={label} />
                      ))}
                    </td>
                    <td>
                      <Avatar
                        user={item.user}
                        onClick={this.handleAuthorClick.bind(this)}
                      />
                    </td>
                    <td>
                      {item.assignees.map(assignee => (
                        <Avatar
                          user={assignee}
                          onClick={this.handleAssigneeClick.bind(this)}
                        />
                      ))}
                    </td>
                    <td>{date(item.created_at)}</td>
                    <td>{date(item.updated_at)}</td>
                    <td>{date(item.closed_at)}</td>
                  </tr>
                ))}
              </table>

              {this.pageCount > 1 && (
                <div>
                  {new Array(this.pageCount).fill(1).map((v, p) => (
                    <button onClick={() => this.handlePageClick(p + 1)}>
                      {page === p + 1 && ">"}
                      {p + 1}
                    </button>
                  ))}
                </div>
              )}
            </div>
          )}
        </div>

        <div
          style={{
            position: 'fixed',
            top: 0,
            right: 0,
            display: !sidebarVisible ? 'block' : 'none',
          }}
        >
          <button onClick={() => this.setState({ sidebarVisible: true})}>show search</button>
        </div>


        <div
          style={{
            display: sidebarVisible ? 'block' : 'none',
            padding: 5,
            background: "#e0e0e0"
          }}
        >
          <div>
            <b>Github Search</b> <button onClick={() => this.setState({ sidebarVisible: false})}>hide</button>
          </div>
          Org:{" "}
          <input
            value={org}
            onChange={e => this.setState({ org: e.target.value })}
          />
          <br />
          Repo:{" "}
          <input
            value={repo}
            onChange={e => this.setState({ repo: e.target.value })}
          />
          <br />
          Opened:{" "}
          <input
            style={{ width: 150 }}
            value={created}
            onChange={e => this.setState({ created: e.target.value })}
          />
          <br />
          Closed:{" "}
          <input
            style={{ width: 150 }}
            value={closed}
            onChange={e => this.setState({ closed: e.target.value })}
          />
          <br />
          Merged:{" "}
          <input
            style={{ width: 150 }}
            value={merged}
            onChange={e => this.setState({ merged: e.target.value })}
          />
          <br />
          Type:{" "}
          <select
            value={is}
            onChange={e => this.setState({ is: e.target.value })}
          >
            <option value="" />
            <option value="issue">issue</option>
            <option value="pr">pr</option>
          </select>
          <br />
          Status:{" "}
          <select
            value={status}
            onChange={e => this.setState({ status: e.target.value })}
          >
            <option value="" />
            <option value="open">open</option>
            <option value="closed">closed</option>
            <option value="merged">merged</option>
            <option value="unmerged">unmerged</option>
          </select>
          <br />
          Assignee:{" "}
          <select
            value={assignee}
            onChange={e => this.setState({ assignee: e.target.value })}
          >
            <option value="" />
            {USERS.map(user => (
              <option value={user}>{user}</option>
            ))}
          </select>
          <br />
          Author:{" "}
          <select
            value={author}
            onChange={e => this.setState({ author: e.target.value })}
          >
            <option value="" />
            {USERS.map(user => (
              <option value={user}>{user}</option>
            ))}
          </select>
          <br />
          Sort:{" "}
          <select
            value={sort}
            onChange={e => this.setState({ sort: e.target.value })}
          >
            <option value="" />
            {this.sortFields.map(sort => (
              <option value={sort}>{sort}</option>
            ))}
          </select>
          <br />
          Order:{" "}
          <select
            value={order}
            onChange={e => this.setState({ order: e.target.value })}
          >
            <option value="desc">desc</option>
            <option value="asc">asc</option>
          </select>
          <br />
          <button onClick={this.handleSearch.bind(this)}>Search</button>

          <hr />
          <div>
            <b>Favorites</b>
          </div>

          {favorites.map(favorite => 
            <div>
              <a href="#" onClick={() => this.handleFavoriteClick(favorite.search)}>{favorite.name}</a>
            </div>
          )}

          Name:{" "}
          <input
            style={{ width: 150 }}
            value={favoriteName}
            onChange={e => this.setState({ favoriteName: e.target.value })}
          />
          <br />
          <button onClick={this.handleAddFavoriteClick.bind(this)}>Add</button>

          <hr />
          <div>
            <b>Settings</b>
          </div>

          {!githubToken && 
            <div>
              <div>Github token:</div>
              <input
                style={{ width: 150 }}
                placeholder='paste'
                value={githubToken}
                onChange={this.handleTokenChange.bind(this)}
              /><br/>
              <a target='_blank' href='https://github.com/settings/tokens'>
                Create personal access token
              </a>
            </div>
          }

          {githubToken && 
            <button onClick={this.handleClearTokenClick.bind(this)}>
              Clear Github token
            </button>
          }

        </div>
      </div>
    );
  }
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);

